"use client";

import React, { useState, useEffect } from "react";
import axios from "axios";
import {
    Button,
    Input,
    Select,
    SelectItem,
    Table,
    TableHeader,
    TableColumn,
    TableBody,
    TableRow,
    TableCell,
    Card,
    CardBody,
    Modal,
    ModalContent,
    ModalHeader,
    ModalBody,
    ModalFooter,
    useDisclosure,
} from "@heroui/react";
import { Eye, Shield } from "lucide-react";

import AnimatedNumber from "@/components/AnimatedNumber";

const EvacAndRepatriation = () => {
    const [currency, setCurrency] = useState("KES");
    const [exchangeRates, setExchangeRates] = useState<Record<string, number>>({});
    const [selectedPlan, setSelectedPlan] = useState<any>(null);
    const { isOpen, onOpen, onClose } = useDisclosure();

    const currencySymbols: Record<string, string> = {
        USD: "$",
        KES: "KSh",
        EUR: "€",
        GBP: "£",
    };

    const insurancePlans = [
        { plan: "Plan 1", limit: 100000, premium: 3500 },
        { plan: "Plan 2", limit: 200000, premium: 7000 },
        { plan: "Plan 3", limit: 300000, premium: 10500 },
        { plan: "Plan 4", limit: 500000, premium: 14400 },
    ];

    useEffect(() => {
        const fetchExchangeRates = async () => {
            try {
                const response = await axios.get(
                    "https://snownet-customer-quotation-server.onrender.com/api/underwriting/quotation/onlineQuotation/products/exchangeRate/"
                );
                const ratesMap: Record<string, number> = {};
                for (let item of response.data?.data || []) {
                    if (item.currency && item.x_rate) {
                        ratesMap[item.currency] = parseFloat(item.x_rate);
                    }
                }
                setExchangeRates(ratesMap);
            } catch (error) {
                console.error("❌ Failed to fetch exchange rates:", error);
            }
        };
        fetchExchangeRates();
    }, []);

    const handleOpenDialog = (plan: any) => {
        setSelectedPlan(plan);
        onOpen();
    };

    const handleClickEvacAndRepatriation = () => {
        window.location.href = "https://quote.birdviewinsurance.com/?ProductID=1";
    };

    return (
        <div className="relative bg-gradient-to-br from-gray-50 via-blue-50 to-white py-12 px-4">
            <div className="max-w-6xl mx-auto bg-white p-6 md:p-10 rounded-3xl shadow-xl border border-gray-200">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
                    {/* Left Content */}
                    <Card shadow="sm" className="border border-gray-100">
                        <CardBody>
                            <h2 className="text-2xl font-semibold text-blue-600 mb-2">
                                Evacuation and Repatriation
                            </h2>
                            <p className="text-gray-600 mb-5">
                                Our worldwide evacuation and repatriation cover is crafted to
                                intervene and rescue situations of citizens being either
                                stranded or deceased abroad. This removes the financial burden
                                of evacuations and/or body repatriations from the government and
                                affected families.
                            </p>
                            <div className="bg-blue-50 p-4 rounded-xl border border-blue-200 shadow-sm">
                                <h3 className="text-xl font-bold text-blue-600 mb-3">
                                    Benefit Summary:
                                </h3>
                                <ul className="space-y-3 text-gray-700 list-disc pl-5">
                                    <li>
                                        <strong>Evacuation on Medical Emergency:</strong> Cash payout{" "}
                                        <span className="text-blue-600 font-bold">25%</span> of
                                        benefit upon evacuation.
                                    </li>
                                    <li>
                                        <strong>Force Return/Deportation:</strong> Cash payout{" "}
                                        <span className="text-blue-600 font-bold">50%</span> of
                                        benefit.
                                    </li>
                                    <li>
                                        <strong>Repatriation in the Event of Demise:</strong> Pay{" "}
                                        <span className="text-blue-600 font-bold">100%</span> of
                                        benefit to the next of kin/nominated beneficiary.
                                    </li>
                                </ul>
                            </div>
                        </CardBody>
                    </Card>

                    {/* Right Pricing Table */}
                    <Card shadow="sm" className="border border-gray-100">
                        <CardBody>
                            <Select
                                label="Select Currency"
                                selectedKeys={[currency]}
                                onChange={(e) => setCurrency(e.target.value)}
                                className="mb-4"
                            >
                                {["KES", "USD", "EUR", "GBP"].map((cur) => (
                                    <SelectItem key={cur}>{cur}</SelectItem>
                                ))}
                            </Select>

                            {/* Desktop Table */}
                            <div className="hidden md:block">
                                <Table aria-label="Pricing Table" isStriped removeWrapper>
                                    <TableHeader>
                                        <TableColumn className="bg-blue-600 text-white text-center">
                                            Plan
                                        </TableColumn>
                                        <TableColumn className="bg-blue-600 text-white text-center">
                                            Benefit Cover
                                        </TableColumn>
                                        <TableColumn className="bg-blue-600 text-white text-center">
                                            Premium
                                        </TableColumn>
                                    </TableHeader>
                                    <TableBody>
                                        {insurancePlans.map((plan, idx) => (
                                            <TableRow
                                                key={idx}
                                                className="cursor-pointer hover:bg-blue-50"
                                                onClick={() => handleOpenDialog(plan)}
                                            >
                                                <TableCell className="text-center">
                                                    <Button
                                                        size="sm"
                                                        color="primary"
                                                        radius="sm"
                                                        endContent={<Eye size={16} />}
                                                    >
                                                        {plan.plan}
                                                    </Button>
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <AnimatedNumber
                                                        value={
                                                            Number(plan.limit) * (exchangeRates[currency] || 1)
                                                        }
                                                        prefix={currencySymbols[currency]}
                                                        color="text-blue-600"
                                                    />
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <AnimatedNumber
                                                        value={
                                                            Number(plan.premium) *
                                                            (exchangeRates[currency] || 1)
                                                        }
                                                        prefix={currencySymbols[currency]}
                                                        color="text-green-600"
                                                    />
                                                </TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                </Table>
                            </div>

                            {/* Mobile Cards */}
                            <div className="grid gap-4 md:hidden">
                                {insurancePlans.map((plan, idx) => (
                                    <Card
                                        key={idx}
                                        shadow="sm"
                                        className="border border-gray-200 cursor-pointer hover:shadow-md"
                                        onClick={() => handleOpenDialog(plan)}
                                    >
                                        <CardBody>
                                            <h3 className="text-lg font-semibold">{plan.plan}</h3>
                                            <p className="text-blue-600 text-sm">
                                                Benefit Cover:{" "}
                                                <AnimatedNumber
                                                    value={
                                                        Number(plan.limit) * (exchangeRates[currency] || 1)
                                                    }
                                                    prefix={currencySymbols[currency]}
                                                    color="text-blue-600"
                                                />
                                            </p>
                                            <p className="text-green-600 text-sm">
                                                Premium:{" "}
                                                <AnimatedNumber
                                                    value={
                                                        Number(plan.premium) *
                                                        (exchangeRates[currency] || 1)
                                                    }
                                                    prefix={currencySymbols[currency]}
                                                    color="text-green-600"
                                                />
                                            </p>
                                        </CardBody>
                                    </Card>
                                ))}
                            </div>
                        </CardBody>
                    </Card>
                </div>

                {/* CTA */}
                <div className="text-center">
                    <p className="text-lg font-semibold text-gray-700 mb-2">
                        Ready to take the next step?
                    </p>
                    <Button
                        size="lg"
                        color="danger"
                        radius="full"
                        className="font-bold shadow-md"
                        endContent={<Shield size={20} />}
                        onClick={handleClickEvacAndRepatriation}
                    >
                        Get Quote
                    </Button>
                </div>
            </div>

            {/* Modal */}
            <Modal isOpen={isOpen} onClose={onClose} size="lg" backdrop="blur">
                <ModalContent>
                    {() => (
                        <>
                            <ModalHeader className="bg-blue-800 text-white">
                                {selectedPlan?.plan} Details
                            </ModalHeader>
                            <ModalBody>
                                {selectedPlan &&
                                    Object.entries(selectedPlan).map(
                                        ([key, value]) =>
                                            key !== "plan" && (
                                                <div
                                                    key={key}
                                                    className="flex justify-between py-2 border-b border-gray-200 text-sm"
                                                >
                                                    <span className="font-medium capitalize text-gray-700">
                                                        {key}
                                                    </span>
                                                    <span className="text-gray-900">
                                                        <AnimatedNumber
                                                            value={
                                                                Number(value) * (exchangeRates[currency] || 1)
                                                            }
                                                            prefix={currencySymbols[currency]}
                                                            color="text-gray-800"
                                                        />
                                                    </span>
                                                </div>
                                            )
                                    )}
                            </ModalBody>
                            <ModalFooter>
                                <Button color="danger" variant="light" onPress={onClose}>
                                    Close
                                </Button>
                            </ModalFooter>
                        </>
                    )}
                </ModalContent>
            </Modal>
        </div>
    );
};

export default EvacAndRepatriation;
